import React, { Component } from "react";
import Slider from "react-slick";
import { hostUrl } from "../../../store/config";
// import Skeleton from 'react-loading-skeleton';
// import Img from 'react-image';

import _ from "lodash";

import { getImageCollection } from "../AttributeUtility";

class ImageSliderList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      nav1: null,
      nav2: null,
      images: null,
    };
  }

  static getDerivedStateFromProps(props, state) {
    // console.log('color :: ' + props.color);
    let images = null;
    if (props.product && props.product["Type"] === "simple") {
      images = props.product.Image;
      // console.log('Images :: ' + JSON.stringify(props.product.Image.split(',')));
    } else {
      images = getImageCollection(props.product && props.product);
    }
    // images && console.log('Images :: ' + JSON.stringify(images))
    return {
      images: images,
      color: props.color,
    };
  }

  handleImg = (event) => {
    // console.log('event Image :: ' + event.target.getAttribute('imgID'));
    this.props.handleImg(event.target.getAttribute("imgID"));
  };

  render() {
    let im =
      this.props.product &&
      this.props.product["Type"] === "simple" &&
      this.state.images &&
      Object.keys(this.state.images).length > 0 &&
      Object.keys(this.state.images.split(",")).length;

    //  console.log('thisss :: ' + (im));

    return (
      <Slider
        // asNavFor={this.state.nav1}
        // ref={slider => (this.state.slider2 = slider)}
        infinite={true}
        speed={500}
        slidesToShow={
          this.props.product && this.props.product["Type"] === "simple"
            ? this.state.images &&
              Object.keys(this.state.images).length > 0 &&
              Object.keys(this.state.images.split(",")).length
            : _.compact(
                this.state.images &&
                  this.state.images.map((item) => {
                    return this.state.color
                      ? item[this.state.color] &&
                          Object.keys(_.uniq(item[this.state.color].split(",")))
                            .length
                      : item && Object.keys(_.uniq(item.split(","))).length;
                  })
              )[0]
        }
        // slidesToShow={8}
        swipeToSlide={true}
        focusOnSelect={true}
        arrows={false}
        dots={false}
        pauseOnHover={true}
        className="slide-thumb"
      >
        {/* // images.map((item) => {
        //     (item[color].split(',')).map((img) => {

        //     })
        // }) */}
        {
          this.props.product && this.props.product["Type"] === "simple"
            ? this.state.images &&
              // <div className='d-flex'>
              this.state.images.split(",").map((img, key) => {
                return (
                  <div>
                    <div>
                      <div className="pro-img-thumb">
                        <img
                          src={`${hostUrl}${img}`}
                          alt=""
                          imgID={key}
                          onClick={this.handleImg}
                        />
                      </div>
                    </div>
                  </div>
                );
              })
            : this.state.images &&
              // <div className='d-flex'>

              this.state.images.map((item) => {
                return this.state.color && this.state.color
                  ? item[this.state.color] &&
                      _.uniq(item[this.state.color].split(",")).map(
                        (img, key) => {
                          return (
                            <div>
                              <div>
                                <div className="pro-img-thumb">
                                  <img
                                    src={`${hostUrl}${img}`}
                                    alt=""
                                    imgID={key}
                                    onClick={this.handleImg}
                                  />
                                </div>
                              </div>
                            </div>
                          );
                        }
                      )
                  : (item && item.split(",")).map((img, key) => {
                      /*console.log('Image :: '+ img);*/
                      return (
                        <div>
                          <div>
                            <div className="pro-img-thumb">
                              <img
                                src={`${hostUrl}${img}`}
                                alt=""
                                imgID={key}
                                onClick={this.handleImg}
                              />
                            </div>
                          </div>
                        </div>
                      );
                    });
              })

          // </div>
        }
        {/* <div>
                    <div className="pro-img-thumb">
                        <img src="/assets/images/products/438x531/4552c.jpg" alt="" />
                    </div>
                </div> */}
      </Slider>
    );
  }
}

export default ImageSliderList;

